* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  background: #b4adad;
  color: #630808;
  font-family: "Nunito", sans-serif;
}

.main {
  width: 80%;
  margin: 0 auto;
}
.main .main__paragraph {
  font-weight: 700;
}
.main .main__paragraph:hover {
  color: rgb(193, 32, 11);
}